import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import Input from '_atoms/input'
import TextArea from '_atoms/text-area'

import styles from './styles.css'

const EmploymentWizard = ({ isOpen, employment, onChange, onToggle }) => (
  <div className={classNames(styles.wizard, { [styles.open]: isOpen })}>
    <button onClick={onToggle} className={styles.header}>
      <div className={styles.titleGroup}>
        <span className={styles.title}>{employment.jobTitle} at {employment.employer}</span>
        <span className={styles.periodDate}>{employment.startDate} - {employment.endDate}</span>
      </div>
      <svg width="24" height="24" viewBox="0 0 24 24" version="1.1" xmlns="http://www.w3.org/2000/svg"><path d="M9.431 7.257l1.352-1.474 5.893 5.48a1 1 0 0 1 0 1.474l-5.893 5.45-1.352-1.475L14.521 12 9.43 7.257z"></path></svg>
    </button>
    <div className={styles.form}>
      <Input 
        id="jobTitle" 
        name="jobTitle" 
        label="Job Title" 
        value={employment.jobTitle} 
        onChange={onChange} 
      />
      <Input 
        id="employer" 
        name="employer" 
        label="Employer" 
        value={employment.employer} 
        onChange={onChange}
      />
      <Input 
        id="city" 
        name="city" 
        label="City" 
        value={employment.city}
        onChange={onChange}
      />
      <div className={styles.periodDateGrid}>
        <Input 
          id="startDate" 
          name="startDate" 
          label="Start Date" 
          value={employment.startDate}
          onChange={onChange}
        />
        <Input 
          id="endDate" 
          name="endDate" 
          label="End Date" 
          value={employment.endDate}
          onChange={onChange}
        />
      </div>
      <TextArea 
        id="description" 
        name="description" 
        label="Description"
        onChange={onChange}
      >
        {employment.description}
      </TextArea>
    </div>
  </div>
)

EmploymentWizard.propTypes = { 
  isOpen: PropTypes.bool, 
  employment: PropTypes.shape({
    jobTitle: PropTypes.string,
    employer: PropTypes.string,
    city: PropTypes.string,
    startDate: PropTypes.string,
    endDate: PropTypes.string,
    description: PropTypes.description,
  }), 
  onChange: PropTypes.func, 
  onToggle: PropTypes.func,
}

EmploymentWizard.defaultProps = { 
  isOpen: false, 
  employment: {
    jobTitle: '',
    employer: '',
    city: '',
    startDate: '',
    endDate: '',
    description: '',
  }, 
  onChange: () => {},
  onToggle: () => {},
}

export default EmploymentWizard
